import React, { Component } from 'react';
import './App.css';
import Layout from './components/Layout/Layout'
import SideMenu from './components/SideMenu/SideMenu';
import Home from './containers/Home/Home';
import { Route, Switch, Redirect, NavLink } from "react-router-dom";
import Admin from './components/Admin/Admin';
import firebase from 'firebase'

firebase.initializeApp({
  databaseURL: 'https://ivfootball-fd9bf.firebaseio.com'
})

class App extends Component {
  state = {
    table: 'table'
  }

  changeTab = (cD) => {
    if (this.state.table !== cD) {
      this.setState({ table: cD })
    }
  }

  render() {
    return (
      <Layout>
        <NavLink
          className='admin'
          to='/admin'
          exact
        >
          Admin
        </NavLink>
        <div className="pr">
          <SideMenu table={this.state.table} changeTabCallback={this.changeTab} />
          <Switch>
            <Route path="/admin" exact component={Admin} />
            <Route path="/" exact render={(props) => <Home {...props} table={this.state.table} />} />
            <Redirect to="/" />
          </Switch>
        </div>
      </Layout>
    );
  }

}

export default App;
