import React, { Component } from "react";
import Aux from "../../hoc/Aux";
import Table from "../../components/Table/Table";
import Schedule from "../../components/Schedule/Schedule";
import firebase from 'firebase'
import Spinner from '../../components/Spinner/Spinner'


class Home extends Component {
    state = {
        teamOrder: [],
        results: [],
        loading: true
    }

    changeTab = (cD) => {
        if (this.props.table !== cD) {
            this.setState({ table: cD })
        }
    }

    componentDidMount() {
        const teamsRef = firebase.database().ref().child('teams')
        const resultRef = firebase.database().ref().child('round')
        teamsRef.on('value', this.getTable);
        resultRef.on('value', this.getResults);
    }

    getTable = (snap) => {
        let teams = snap.val();
        const teamsArr = [];
        let index = 0;
        for (let key in teams) {
            if (teams.hasOwnProperty(key)) {
                index++
                teamsArr.push({ ...teams[key], id: index })
            }
        }
        teamsArr.sort((a, b) => (a.score < b.score) ? 1 : -1);
        this.setState({ teamOrder: teamsArr, loading: false })
    }
    getResults = (snap) => {
        let results = snap.val();
        this.setState({ results: results })
    }

    render() {
        let activeContent = '';
        let table = this.state.loading ? <Spinner /> : <Table teams={this.state.teamOrder} />

        if (this.props.table === 'table') {
            activeContent = table;
        }
        else {
            activeContent = <Schedule results={this.state.results} />
        }
        return (
            <Aux>
                {activeContent}
            </Aux>
        )
    }
}

export default Home;